@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
:root{
  --typing-box-width: 550px;
  --typing-box-height: 375px;
  --page-color: rgb(18, 18, 18);
  --shade-1: rgb(30, 30, 30);
}

body {
  font-family: 'Roboto Mono', monospace;
  background-color: rgb(18, 18, 18);
  background-color: var(--page-color);

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.main-content{
  height: 500px;
  margin-top: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
}

.typing-box{
  width: 550px;
  width: var(--typing-box-width);
  height: 375px;
  height: var(--typing-box-height);
  border-radius: 8px;
  background-color: rgb(30, 30, 30);
  background-color: var(--shade-1);
}

.typing-text{
  padding: 20px;
  position: relative;
  width: calc(550px - 40px);
  width: calc(var(--typing-box-width) - 40px);
  height: auto;
  font-size: 20px;
  color: white;
}
:root{
  --hover-color: rgba(255, 255, 255, 0.13);
  --icon-color: rgba(255, 255, 255, 0.5);
}

.header-container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;

  margin-bottom: 10px;
}

.stats-container{
  width: 250px;
  border-radius: 6px;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}

.paste-container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  width: 60px;
  height: 60px;
  background-color: var(--shade-1);
  border-radius: 6px;
}

.paste-container:hover{
  background-color: rgba(255, 255, 255, 0.13);
  background-color: var(--hover-color);
  transition: background-color 0.2s;
}

.next-container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  position: absolute;
  margin-left: 70px;
  width: 60px;
  height: 60px;
  background-color: var(--shade-1);
  border-radius: 6px;
}

.next-container:hover{
  background-color: rgba(255, 255, 255, 0.13);
  background-color: var(--hover-color);
  transition: background-color 0.2s;
}

